@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,500;0,700;0,800;0,900;1,400;1,500;1,600&display=swap');
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  padding-bottom: 100px;
  overflow-x: hidden;
  scroll-behavior: smooth;
}

::-webkit-scrollbar
{
	width: 4px;
	background-color: #000;
}

::-webkit-scrollbar-thumb
{
	background-color: rgba(134, 130, 130, 0.801);
	background-image: black;
}

body {
  background-color: #010302;
  min-height: 200vh;
}


li,
a,
button {
  font-weight: 500;
  font-size: 16px;
  color: #edf0f1;
  text-decoration: none;
	
}
*,a {
  font-family: "Poppins", sans-serif;
  -webkit-user-drag: none;
	-webkit-app-region: no-drag;
  -moz-user-select: -moz-none;
  -khtml-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}




header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  transition: 0.6s;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 40px 15%;
  z-index: 100;
}



header.sticky {
  padding: 10px 10%;
  background: #000;
}

.logo{
  display: none;
  
}

.nav__link {
  list-style: none;
}

.nav__link li {
  display: inline-block;
  padding: 0 20px;
}

.nav__link li a {
  transition: all 1s ease 0s;
  letter-spacing: 2px;
}



.nav__link li a:hover {
  color: aqua;
}
.nav__link li a:active{
  color: aqua;
}
.cta {
    color: black;
  padding: 10px 25px;
  background-color: aqua;
  border: none;
  border-radius: 50px;
  cursor: pointer;
  transition: all 1s ease 0s;
}

.cta:hover {
  background-color: greenyellow;
}




#hide{
  font-size: 40px;
  font-weight: 600;
}


.bottom-btm  {
  font-size: 12px;
  position: absolute;
  top: 90%;
  left: 5%;
}


.bottom-top {
  
  -webkit-text-stroke: 1px #333;
  color: white;
  position: absolute;
  top: 55%;
  left: 5%;
}


.bottom-top2 {
  color: black;
  position: absolute;
  top: 55%;
  left: 5%;
}

@media screen and (max-width:630px) {
  header.sticky {
    padding: 12px 15%;
  }
  .nav__link{
    padding-left: 20px;
  }
  
  .nav__link li{
    
    padding: 0 5px;
  }

  .cta{
    display: none;
  }
  #hide{
    font-size: 35px;
  }
  .bottom-top{
    top:60%;
    left: 5%;
  }
  
}


section{
  padding: 6% 10%;
  color: white;
}
section h1{
  font-size: 35px;
  text-align: center;
}

.about-info{
  display: flex;
  flex-direction: row;
}
.profile,.skills{
    padding: 2%;
    max-width: 50%;
    text-align: justify;
}
.profile p:nth-child(2),p:nth-child(3) {
  padding-top: 20px;
}
.skills img{
  padding-top: 25px;
}
@media screen and (max-width:1100px) {
  .about-info{
    flex-direction: column;
  }
  .profile,.skills{
    max-width: 100%;
    
  }
}
