:root {
  --f-transition: 0.2s; /*  Fast Transition */
  --main-transition: 0.3s; /* Main Transition */
  --m-transition: 0.4s; /* Medium Transition */
  --s-transition: 0.5s; /* Slow Transition */
  --header-height: 60px;
  --sections-padding: 60px;
  --main-color: black;
  --second-color: #111;
  --third-color: hsl(179, 100%, 40%);
  --light-main-color: hsl(206, 92%, 50%);
  --light-second-color: hsl(247, 74%, 67%);
  --light-third-color: hsl(179, 100%, 44%);
}
.container{
  padding-top: 5%;
}
.projects {
  padding-top: var(--sections-padding);
  padding-bottom: var(--sections-padding);
 
}
.projects-content {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 30px;
}
.projects-content .project {
  position: relative;
  border-radius: 6px;
  background: white;
  overflow: hidden;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.07);
}
.project .project-details {
  padding: 15px;
  text-align: start;
  color: #000;
}
.project .project-tile {
  text-transform: capitalize;
  margin-bottom: 8px;
  color: var(--main-color);
  font-size: 22px;
  font-weight: 600;
}
.project .project-description {
  font-size: 15px;
  line-height: 1.6;
  color: #555;
}
 .more-projects {
   padding-top: 15px;
  text-align: center;
}


.footer-distributed {
  background-color: #000;
  box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.12);
  box-sizing: border-box;
  width: 100%;
  text-align: left;
  font: normal 16px sans-serif;
  padding: 30px 50px;
}

.footer-distributed .footer-left p {
  color: #444;
  font-size: 15px;
  margin: 0;
}


.footer-distributed .footer-right {
  float: right;
  max-width: 180px;
}

.footer-distributed .footer-right a {
  display: inline-block;
  width: 37px;
  height: 35px;
  border-radius: 2px;
  font-size: 20px;
  color: #ffffff;
  text-align: center;
  line-height: 35px;
  margin-left: 3px;
  transition:all .25s;
}

.footer-distributed .footer-right a:hover{transform:scale(1.3); -webkit-transform:scale(1.3);}

.footer-distributed p.footer-links a:hover{text-decoration:underline;}

/* Media Queries */

@media (max-width: 600px) {
  .footer-distributed .footer-left, .footer-distributed .footer-right {
    text-align: center;
  }
  .footer-distributed .footer-right {
    float: none;
    margin: 0 auto 20px;
  }

}

